
<template>
<div class="box">
   <Header>
      <div slot="center">用户地址编辑</div>
    </Header>
    <div class="content">
       <van-address-edit
        :area-list="areaList"
        :address-info="addressInfo"
        save-button-text="修改"
        show-postal
        show-delete
        show-set-default
        show-search-result
        :search-result="searchResult"
        :area-columns-placeholder="['请选择', '请选择', '请选择']"
        @save="onSave"
        @delete="onDelete"
        @change-detail="onChangeDetail"
      />
    </div>
  </div>

</template>
<script>
import Header from '@/components/Header'
import axios from '@/utils/request'
import city from '@/utils/city'
import Vue from 'vue'
import { AddressEdit, Toast } from 'vant'
Vue.use(AddressEdit)
Vue.use(Toast)
export default {
  components: {
    Header
  },
  data () {
    return {
      areaList: city,
      searchResult: [],
      addressInfo: {}
    }
  },
  mounted () {
    console.log(this.$route.query)
    let url = '/address/select?addressid=' + this.$route.query.addressid
    axios.get(url).then(res => {
      console.log(res)
      this.addressInfo = {
        name: res.data.data[0].name,
        tel: res.data.data[0].tel,
        province: res.data.data[0].province,
        city: res.data.data[0].city,
        county: res.data.data[0].county,
        areaCode: res.data.data[0].areaCode,
        postalCode: res.data.data[0].postalCode,
        addressDetail: res.data.data[0].addressDetail
      }
    })
  },
  methods: {
    onSave (content) {
      console.log(content)
      axios.post('/address/update', {
        addressid: this.$route.query.addressid,
        userid: localStorage.getItem('userid'),
        name: content.name,
        tel: content.tel,
        province: content.province,
        city: content.city,
        county: content.county,
        addressDetail: content.addressDetail,
        flag: content.isDefault,
        areaCode: content.areaCode,
        postalCode: content.postalCode
      }).then(() => {
        Toast('地址修改成功')
        this.$router.back()
      })
    },
    onDelete () {
      axios.post('/address/delete', {
        addressid: this.$route.query.addressid
      }).then(() => {
        Toast('删除成功')
        this.$router.back()
      })
    },
    onChangeDetail (val) {
    },
    goback () {
      this.$router.back()
    }
  }
}
</script>
